.hero{
  position: relative;
  display: grid;
  align-content: center;
  aspect-ratio: 1.6386;
  inline-size: 100%;
  margin-block-start: clamp(0px, 3.4412rem + -4.7059vi, 40px);
  &__subtitle{
    max-inline-size: 511px;
  }
  &__img{
    position: absolute;
    z-index: -1;
    inset-inline-end: 0;
    inset-block-start: 9%;
    inline-size: 62.47%;
    object-fit: unset;

  }
}
@media (max-width: 429px) {
  .hero{
    &__img{
      display: none;
    }
  }
}
