.n-projects{
  padding: clamp(15px, -0.5919rem + 7.6471vi, 80px);
  padding-block-end: clamp(24px, 0.8882rem + 3.0588vi, 50px);
  &__title-block{
    display: flex;
    justify-content: space-between;
  }
  &__logo{
    order: 1;
    flex-shrink: 0;
    aspect-ratio: 1.4528;
    inline-size: clamp(113px, 4.286rem + 13.8824vi, 231px);
  }
  &__title{
    order: 0;
    max-inline-size: 714px;
  }
  &__btns{
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    span:nth-child(3){
      margin-inline-end: auto;
      @media (max-width: 640px) {
        margin-inline-end: unset;
      }
    }
  }
}
@media (max-width: 640px) {
  .n-projects{
    &__title-block{
      display: grid;
      gap: 10px;
    }
    &__logo{
      order: 0;
      justify-self: end;
    }
    &__title{
      max-inline-size: unset;
    }
    &__btns{
      span:nth-child(3){
        margin-inline-end: unset;
      }
    }
  }
}
@media (max-width: 429px) {
  .n-projects{
    &__btns{
      display: grid;
      gap: 10px;
      &>*{
        justify-self: start;
      }
      .btn{
        margin-block-start: 10px;
      }
    }
  }
}
