.mission{
  &__inner{
    --btm-w: 864px;
    --btm-mi: -150px;
    --btm-mis: 535px;
    --btm-mbs: -655px;
    position: relative;
    margin-inline: var(--btm-mi) 0;
    margin-block-start: -100px;
    &-top{
      position: relative;
      display: grid;
      place-items: center;
      aspect-ratio: 1;
      inline-size: 655px;
      background: linear-gradient(180deg, hsl(0 100% 99% / .7) 0%, hsl(0 0% 100% / .9) 100%);
      border-radius: 100vw;
      &::before{
        content: '';
        position: absolute;
        z-index: 0;
        inset-inline-start: 50%;
        inset-block-start: 50%;
        transform: translate(-50%,-50%);
        aspect-ratio: 1;
        inline-size: 91.6%;
        background-color: var(--clr-white);
        border: 1px solid #FFF1EF;
        border-radius: 100vw;
      }
      &::after{
        content: '';
        position: absolute;
        z-index: 1;
        inset-inline-start: 50%;
        inset-block-start: 50%;
        transform: translate(-50%,-50%);
        aspect-ratio: 1;
        inline-size: 83.51%;
        border: 1px solid var(--clr-accent-pink-roza);
        border-radius: 100vw;
      }
      &-box{
        position: relative;
        z-index: 2;
        display: grid;
        align-content: center;
        aspect-ratio: 1;
        inline-size: 71.9%;
        padding-inline: 8%;
        border: 1px solid hsl(357 87% 91% / .5);
        border-radius: 100vw;
      }
      &-title{
        font-size: clamp(18px, 0.6074rem + 2.5882vi, 40px);
      }
      &-text{
        font-size: clamp(12px, 0.5618rem + 0.9412vi, 20px);
        font-weight: var(--fw-m);
      }
    }
    &-btm{
      --circle-s: clamp(8px, 0.1429rem + 1.7857vi, 20px);
      position: relative;
      z-index: 2;
      margin-inline-start: var(--btm-mis);
      margin-block-start: var(--btm-mbs);
      display: grid;
      place-items: center;
      aspect-ratio: 1;
      inline-size: var(--btm-w);
      background: linear-gradient(180deg, hsl(0 100% 99% / .7) 0%, hsl(0 0% 100% / .9) 100%);
      border-radius: 100vw;
      &::before{
        content: '';
        position: absolute;
        z-index: 0;
        inset-inline-start: 50%;
        inset-block-start: 50%;
        transform: translate(-50%,-50%);
        aspect-ratio: 1;
        inline-size: 95.13%;
        background: linear-gradient(180deg, #ffeff0 0%, #fff6f6 100%);
        border: 1px solid #FFF1EF;
        border-radius: 100vw;
      }
      .circle1-container{
        --cont-w: calc(var(--btm-w) * .8842);
        position: absolute;
        z-index: 1;
        inset-inline-start: 50%;
        inset-block-start: 50%;
        transform: translate(-50%,-50%);
        aspect-ratio: 1;
        inline-size: var(--cont-w);
        background-image: url('../img/misc/flower-linear-pink.webp');
        background-repeat: no-repeat;
        background-position: left 68% top 28%;
        background-size: 59.42% auto;
        border: 4px solid hsl(0 0% 100% / .7);
        border-radius: 100vw;
        #circle1{
          position: absolute;
          inset-inline-start: calc(-1 * var(--circle-s) / 2);
          inset-block-start: calc(50% - var(--circle-s) / 2);
          background-color: var(--clr-white);
          inline-size: var(--circle-s);
          block-size: var(--circle-s);
          border-radius: 100vw;
          box-shadow: 0px 0px 8px hsl(0 0% 0% / .075);
          transform-origin: calc(var(--cont-w) / 2.015 + var(--circle-s) / 2) center;
          animation: circle1 15s infinite linear;
        }
      }
      &-box{
        --box-w: calc(var(--btm-w) * .8043);
        position: relative;
        z-index: 2;
        display: grid;
        align-content: center;
        aspect-ratio: 1;
        inline-size: var(--box-w);
        padding-inline: 5%;
        border: 3px solid hsl(0 0% 100% / .7);
        border-radius: 100vw;
        #circle2{
          position: absolute;
          inset-inline-start: calc(-1 * var(--circle-s) / 2);
          inset-block-start: calc(50% - var(--circle-s) / 2);
          background-color: var(--clr-white);
          inline-size: var(--circle-s);
          block-size: var(--circle-s);
          border-radius: 100vw;
          box-shadow: 0px 0px 8px hsl(0 0% 0% / .075);
          transform-origin: calc(var(--box-w) / 2.015 + var(--circle-s) / 2) center;
          animation: circle2 10s infinite linear;
        }
      }
      &-title{
        inline-size: min-content;
        font-size: clamp(20px, 0.5441rem + 3.5294vi, 50px);
        text-transform: uppercase;
        text-shadow: 3px 0 0 #fff1f3, -3px 0 0 #fff1f3, 0 3px 0 #fff1f3, 0 -3px 0 #fff1f3;
      }
      &-text{
        max-inline-size: 361px;
        font-size: clamp(12px, 0.5618rem + 0.9412vi, 20px);
        font-weight: var(--fw-b);
        line-height: 1.2;
      }
    }
    &-list{
      position: absolute;
      z-index: 2;
      inset-block-start: calc(1210px + var(--btm-mbs));
      inset-inline-start: calc(165px + var(--btm-mis));
      li{
        position: absolute;
      }
      li:nth-child(1){
        inline-size: 215px;
      }
      li:nth-child(2){
        inset-inline-start: 150px;
        inset-block-start: 70px;
        inline-size: 215px;
      }
      li:nth-child(3){
        inset-inline-start: 270px;
        inset-block-start: 140px;
        inline-size: 174px;
      }
    }
  }
}
@media (max-width: 1350px) {
  .mission{
    &__inner{
      --btm-mi: 0px;
      --btm-mis: 0px;
      --btm-mbs: -200px;
      display: grid;
      justify-items: center;
      &-list{
        inset-inline-start: calc(25vw + var(--btm-mis));
      }
    }
  }
}
@media (max-width: 1150px) {
  .mission{
    &__inner{
      &-top{
        &-title{
          inline-size: clamp(180px, 5.1563rem + 25vi, 370px);
        }
      }
    }
  }
}
@media (max-width: 992px) {
  .mission{
    &__inner{
      --btm-w: clamp(320px, 0rem + 100vi, 992px);
      &-top{
        inline-size: clamp(280px, 6.0417rem + 57.2917vi, 665px);
      }
      &-btm{
        margin-inline: calc(-1 * var(--minimum-content-padding));
        margin-block-start: clamp(-200px, 1.3393rem + -22.3214vi, -50px);
        margin-block-end: clamp(10px, -0.2679rem + 4.4643vi, 40px);
        .circle1-container{
          border-width: 3px;
        }
        &-box{
          padding-inline: 11vw;
          border-width: 2px;
        }
        &-title{
          position: relative;
          inset-block: 9vw;
          font-size: 7vw;
        }
        &-text{
          position: relative;
          inset-block: 9vw;
          max-inline-size: unset;
          inline-size: 57vw;
          font-size: 3.5vw;
        }
      }
      &-list{
        position: static;
        display: grid;
        grid-template-columns: repeat(3,1fr);
        gap: 20px var(--gutter);
        li:nth-child(1),li:nth-child(2),li:nth-child(3){
          position: static;
          transform: unset;
          inline-size: unset;
          font-size: clamp(8px, 0.1429rem + 1.7857vi, 20px);
          text-align: center;
        }
      }
    }
  }
}
@media (max-width: 640px) {
  .mission{
    &__inner{
      margin-block-start: clamp(-100px, 6.25rem + -31.25vi, 0px);
    }
  }
}
@media (max-width: 390px) {
  .mission{
    &__inner{
      &-top{
        &-title{
          font-size: 5.05vw;
          inline-size: clamp(150px, 0.8036rem + 42.8571vi, 180px);
        }
      }
    }
  }
}
@media (max-width: 330px) {
  .mission{
    &__inner{
      &-top{
        &-title{
          font-size: 5.15vw;
        }
      }
    }
  }
}

