.header{
  position: relative;
  z-index: 1;
  display: grid;
  align-items: center;
  block-size: var(--header-h);
  &__inner{
    position: relative;
    display: flex;
    align-items: center;
    gap: 0 40px;
    block-size: 100%;
    &-logo{
      block-size: clamp(46px, 2.7338rem + 0.7059vi, 52px);
      margin-inline-end: auto;
      img{
        block-size: 100%;
      }
      .logo-mobile{
        display: none;
      }
    }
    &-nav{
      visibility: hidden;
      opacity: 0;
      transform: translateY(-10px);
      position: absolute;
      inset-inline-end: 0;
      inset-block-start: 100%;
      transition: visibility var(--tr), opacity var(--tr), transform var(--tr);
      &.active{
        visibility: visible;
        opacity: 1;
        transform: translateY(0px);
      }
    }
    &-lang{
      display: flex;
      gap: 0 15px;
      span{
        cursor: pointer;
      }
    }
    &-burger{
      --size: 30px;
      --divider: 3.3;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      inline-size: var(--size);
      block-size: var(--size);
      padding: 5px;
      div{
        inline-size: 100%;
        block-size: 2px;
        background-color: var(--clr-accent-pink);
        border-radius: 100vw;
        transition: transform var(--tr), opacity var(--tr);
      }
      &.active{
        div:nth-child(1){
          transform: translateY(calc(var(--size) / var(--divider))) rotate(45deg);
        }
        div:nth-child(2){
          opacity: 0;
        }
        div:nth-child(3){
          transform: translateY(calc(-1 * var(--size) / var(--divider))) rotate(-45deg);
        }
      }
    }
  }
}
.prim-menu{
  display: grid;
  gap: 10px 0;
  &__item{}
  &__link{
    text-underline-offset: 3px;
    text-decoration-line: underline;
    text-decoration-color: transparent;
    transition: text-decoration-color var(--tr);
  }
}
@media (max-width: 640px) {
  .header{
    .logo{
      display: none;
    }
    .logo-mobile{
      display: block;
    }
  }
}
