.dnkom{
  display: grid;
  grid-template-columns: repeat(12,1fr);
  gap: 0 var(--gutter);
  &__top{
    position: relative;
    grid-column: 1 / span 7;
    grid-row: 1 / span 1;
    display: grid;
    gap: clamp(10px, 0.1544rem + 2.3529vi, 30px) 0;
    padding-block-start: clamp(60px, 2.8088rem + 4.7059vi, 100px);
  }
  &__logo{
    aspect-ratio: 3.1840;
    inline-size: clamp(121px, 2.8801rem + 23.4118vi, 320px);
  }
  &__info{
    max-inline-size: 330px;
  }
  &__btm{
    grid-column: 1 / -1;
    grid-row: 1 / span 2;
    justify-self: end;
    &-img{
      margin-block-start: clamp(0px, 14rem + -21.875vi, 154px);
      aspect-ratio: 2.5884;
      inline-size: clamp(226px, 5.6779rem + 42.2353vi, 585px);
    }
  }
}
@media (max-width: 1024px) {
  .dnkom{
    &__top{
      grid-column: 1 / span 8;
    }
    &__title{
      max-inline-size: 600px;
    }
  }
}
