.splide{
  &__track{
    margin-inline: 7.26%;
  }
  &__arrows{
    position: absolute;
    inset-inline-start: 50%;
    inset-block-start: 50%;
    transform: translate(-50%,-50%);
    display: flex;
    justify-content: space-between;
    inline-size: 95.81%;
  }
  &__arrow{
    --s: clamp(26px, 0.9191rem + 3.5294vi, 56px);
    all: unset;
    inline-size: var(--s);
    block-size: var(--s);
    cursor: pointer;
    &--prev, &--next{
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
    }
    &--prev{
      background-image: url("data:image/svg+xml,%3Csvg width='63' height='63' viewBox='0 0 63 63' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M0.999914 31.2801C0.998797 31.812 1.20959 32.3224 1.5857 32.6985L29.87 60.9828C30.651 61.7639 31.9173 61.7639 32.6984 60.9828C33.4794 60.2018 33.4794 58.9354 32.6984 58.1544L5.8343 31.2903L32.8114 4.53915C33.5957 3.76139 33.601 2.49507 32.8233 1.71075C32.0455 0.926419 30.7792 0.921094 29.9949 1.69885L1.59166 29.8642C1.21397 30.2387 1.00103 30.7482 0.999914 31.2801Z' fill='white'/%3E%3C/svg%3E%0A");
    }
    &--next{
      background-image: url("data:image/svg+xml,%3Csvg width='63' height='63' viewBox='0 0 63 63' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M61.5714 31.2801C61.5725 31.812 61.3617 32.3224 60.9856 32.6985L32.7013 60.9828C31.9203 61.7639 30.6539 61.7639 29.8729 60.9828C29.0918 60.2018 29.0918 58.9354 29.8729 58.1544L56.737 31.2903L29.7599 4.53915C28.9756 3.76139 28.9703 2.49507 29.748 1.71075C30.5258 0.926419 31.7921 0.921094 32.5764 1.69885L60.9796 29.8642C61.3573 30.2387 61.5703 30.7482 61.5714 31.2801Z' fill='white'/%3E%3C/svg%3E");
    }
  }
}
.slider{
  &__slide{
    position: relative;
    display: grid;
    &::before{
      content: '';
      position: absolute;
      z-index: 1;
      inset: 0%;
      background-image: url('../img/misc/flower-linear.webp');
      background-repeat: no-repeat;
      background-position: top -65% right -63%;
      background-size: 63.51% auto;
    }
    &::after{
      content: '';
      position: absolute;
      z-index: 0;
      inset: 0;
      background: linear-gradient(180deg, #FFDFDF 0%, #FFFAFA 100%);
      opacity: .5;
    }
    p{
      position: relative;
      z-index: 2;
      font-size: clamp(18px, 0.6074rem + 2.5882vi, 40px);
    }
    &-btn{
      position: relative;
      z-index: 2;
    }
  }
}
@media (max-width: 768px) {
  .slider{
    &__slide{
      &::before{
        background-position: top 40px center;
        background-size: 500px auto;
      }
      &-btn{
        justify-self: center;
      }
    }
  }
}
@media (max-width: 429px) {
  .splide{
    &__arrows{
      inline-size: 100%;
    }
  }
}

