.cards-state.auto-grid{
  --min-w: 310px;
}
.card-state{
  position: relative;
  display: grid;
  justify-items: center;
  padding-inline: 10px;
  block-size: clamp(300px, 16.3971rem + 11.7647vi, 400px);
  padding-block: 40px clamp(26px, 1.2956rem + 1.6471vi, 40px);
  &__img{
    position: absolute;
    z-index: -1;
    inset: 0;
    img{
      block-size: 100%;
    }
  }
  &__title{
    color: var(--clr-white);
    font-family: var(--ff-sec);
    font-size: clamp(20px, 1.1794rem + 0.3529vi, 23px);
    font-weight: var(--fw-b);
    text-align: center;
  }
  &__btn{
    align-self: end;
  }
}
