.cards-eko-blago{
  display: grid;
  grid-template-columns: repeat(2,1fr);
  gap: var(--gutter);
}
.card-eko-blago{
  overflow: hidden;
  position: relative;
  display: grid;
  grid-template-rows: 1fr min-content;
  justify-items: center;
  block-size: clamp(280px, 13.0294rem + 22.3529vi, 470px);
  padding-block-end: clamp(24px, 1.1235rem + 1.8824vi, 40px);
  &__img{
    position: absolute;
    aspect-ratio: .8263;
    inline-size: clamp(279px, 12.9199rem + 22.5882vi, 471px);
  }
  .card-1{
    inset-inline-start: 50%;
    inset-block-start: -100px;
    transform: translateX(-50%);
    @media (max-width: 768px) {
      inset-block-start: unset;
      inset-block-end: clamp(-270px, -4.0179rem + -26.7857vi, -150px);
    }
  }
  .card-2{
    inset-inline-start: 50%;
    inset-block-end: -270px;
    transform: translateX(-50%);
    @media (max-width: 768px) {
      inset-block-end: clamp(-270px, -4.0179rem + -26.7857vi, -150px);
    }
  }

  &__title{
    position: relative;
    z-index: 2;
    align-self: center;
    font-size: clamp(17px, 0.5213rem + 2.7059vi, 40px);
  }
  .btn{
    position: relative;
    z-index: 2;
  }
}

@media (max-width: 1200px) {
  .cards-eko-blago{
    grid-template-columns: 1fr;
    &>*{
      justify-self: center;
    }
  }
  .card-eko-blago{
    max-inline-size: 570px;
    padding-inline: clamp(24px, 1.1235rem + 1.8824vi, 40px);
  }
}
