.news-wrap{}
.cards-news.auto-grid{
  --min-w: 310px;
  --r-g: 40px;
}
.card-news{
  display: grid;
  grid-template-rows: min-content;
  gap: clamp(16px, 0.6706rem + 1.6471vi, 30px);
  color: var(--clr-black);
  transition: color var(--tr);
  &__img{
    overflow: hidden;
    block-size: clamp(220px, 12.5735rem + 5.8824vi, 270px);
    border-radius: 40px;
    border: 3px solid transparent;
    img{
      block-size: 100%;
      transition: transform var(--tr);
    }
  }
  &__text{
    font-size: clamp(16px, 0.9059rem + 0.4706vi, 20px);
    line-height: 1.3;
  }
  &.hide{
    display: none;
  }
  @media(hover: hover){
    &:hover{
      color: var(--clr-accent-pink);
      .card-news__img{
        img{
          transform: scale(1.05);
        }
      }
    }
  }
}
@media (max-width: 429px) {
  .cards-news.auto-grid.cards-news--scroll{
    display: flex;
    gap: 0 8px;
    overflow-x: auto;
    margin-inline: calc(-1 * var(--minimum-content-padding));
    margin-block-end: 20px;
    padding-inline: var(--minimum-content-padding);
    &::-webkit-scrollbar {
      display: none;
    }
    scrollbar-width: none;
    .card-news{
      min-inline-size: 280px;
    }
  }
  .news-all-btn{
    position: static;
  }
}
