.why{
  display: grid;
  grid-template-columns: repeat(12,1fr);
  gap: var(--gutter);
  &__text{
    grid-column: 1 / span 6;
    grid-row: 1 / span 1;
    max-inline-size: 467px;
  }
  &__info{
    justify-self: end;
    align-self: center;
    grid-column: 7 / -1;
    grid-row: 1 / span 1;
    display: grid;
    gap: 5px;
    max-inline-size: 487px;
  }
}
@media (max-width: 768px) {
  .why{
    grid-template-columns: 1fr;
    &__text{
      grid-column: 1 / -1;
      grid-row: 1 / span 1;
      max-inline-size: unset;
    }
    &__info{
      justify-self: center;
      justify-items: center;
      grid-column: 1 / -1;
      grid-row: 2 / span 1;
      max-inline-size: 460px;
      text-align: center;
    }
  }
}
