.input{
  display: grid;
  justify-items: start;
  inline-size: 100%;
  block-size: 60px;
  padding-inline: 30px;
  background-color: var(--clr-white);
  border: 1px solid var(--clr-border);
  border-radius: 100vw;
  &::placeholder{
    color: #787878;
  }
  &:focus-visible::placeholder{
    color: transparent;
  }
  &:focus-visible{
    outline: none;
  }
}
textarea{
  min-block-size: 176px;
  inline-size: 100%;
  padding: 18px 30px;
  background-color: var(--clr-white);
  border: 1px solid var(--clr-border);
  border-radius: 30px;
  &::placeholder{
    color: #787878;
  }
  &:focus-visible::placeholder{
    color: transparent;
  }
  &:focus-visible{
    outline: none;
  }
}
