.auto-grid {
  --min-w: 250px;
  --max-w: 1fr;
  --c-g: 1.25rem;
  --r-g: 1.25rem;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(min(var(--min-w), 100%), var(--max-w)));
  column-gap: var(--c-g);
  row-gap: var(--r-g);
}
.disbalance.auto-grid{
  --min-w: 310px;
}
// LAYOUTS --------------------------------------
.popout{
  padding-block-start: clamp(35px, 1.1287rem + 5.2941vi, 80px);
  padding-block-end: clamp(45px, 1.0478rem + 8.8235vi, 120px);
  background-color: #F79397;
}
// OVERFLOW --------------------------------------
.ov-h{
  overflow: hidden;
}
// POSITIONS -------------------------------------
.pos-r{
  position: relative;
}
.pos-a{
  position: absolute;
}
.iis-0{
  inset-inline-start: 0;
}
.iie-0{
  inset-inline-end: 0;
}
.ibs-0{
  inset-block-start: 0;
}
.ibe-0{
  inset-block-end: 0;
}
// PADDINGS --------------------------------------
.p-20{
  padding: 20px;
}
.p-40-20{
  padding: clamp(20px, 0.7794rem + 2.3529vi, 40px);
}
.p-50-25{
  padding: clamp(25px, 0.9743rem + 2.9412vi, 50px);
}
.pb-0{
  padding-block: 0;
}
.pb-30{
  padding-block: 30px;
}
.pb-50-25{
  padding-block: clamp(25px, 0.9743rem + 2.9412vi, 50px);
}

// MARGINS --------------------------------------
.mbs-60-45{
  margin-block-start: clamp(45px, 2.4596rem + 1.7647vi, 60px);
}
.mbs-100-45{
  margin-block-start: clamp(45px, 1.5184rem + 6.4706vi, 100px);
}
.mbe-0{
  margin-block-end: 0;
}
.mbe-15{
  margin-block-end: 15px;
}
.mbe-20{
  margin-block-end: 20px;
}
.mbe-20-10{
  margin-block-end: clamp(10px, 0.3897rem + 1.1765vi, 20px);
}
.mbe-30{
  margin-block-end: 30px;
}
.mbe-60{
  margin-block-end: 60px;
}
.mbe-40-20{
  margin-block-end: clamp(20px, 0.7794rem + 2.3529vi, 40px);
}
.mbe-60-20{
  margin-block-end: clamp(20px, 0.3088rem + 4.7059vi, 60px);
}
.mbe-100-60{
  margin-block-end: clamp(60px, 2.8088rem + 4.7059vi, 100px);
}
.mis-a{
  margin-inline-start: auto;
}
// FONTS --------------------------------------
.ff-prim{
  font-family: var(--ff-prim);
}
.ff-sec{
  font-family: var(--ff-sec);
}
// FONT SIZES ---------------------------------
