}
.fz-20{
  font-size: 20px;
  line-height: 1.1;
}
.fz-20-12{
  font-size: clamp(12px, 0.5618rem + 0.9412vi, 20px);
  line-height: 1.3;
}
.fz-20-15{
  font-size: clamp(15px, 0.8199rem + 0.5882vi, 20px);
  line-height: 1.3;
}
.fz-22-12{
  font-size: clamp(12px, 0.5147rem + 1.1765vi, 22px);
