}
// TEXT ----------------------------------------
.tac{
  text-align: center;
}
// TEXT SHADOW ---------------------------------
.smooth-16 {
  text-shadow: calc(var(--stroke-width) * 1) calc(var(--stroke-width) * 0) 0
      var(--stroke-color),
    calc(var(--stroke-width) * 0.9239) calc(var(--stroke-width) * 0.3827) 0
      var(--stroke-color),
    calc(var(--stroke-width) * 0.7071) calc(var(--stroke-width) * 0.7071) 0
      var(--stroke-color),
    calc(var(--stroke-width) * 0.3827) calc(var(--stroke-width) * 0.9239) 0
      var(--stroke-color),
    calc(var(--stroke-width) * 0) calc(var(--stroke-width) * 1) 0
      var(--stroke-color),
    calc(var(--stroke-width) * -0.3827) calc(var(--stroke-width) * 0.9239) 0
      var(--stroke-color),
    calc(var(--stroke-width) * -0.7071) calc(var(--stroke-width) * 0.7071) 0
      var(--stroke-color),
    calc(var(--stroke-width) * -0.9239) calc(var(--stroke-width) * 0.3827) 0
      var(--stroke-color),
    calc(var(--stroke-width) * -1) calc(var(--stroke-width) * 0) 0
      var(--stroke-color),
    calc(var(--stroke-width) * -0.9239) calc(var(--stroke-width) * -0.3827) 0
      var(--stroke-color),
    calc(var(--stroke-width) * -0.7071) calc(var(--stroke-width) * -0.7071) 0
      var(--stroke-color),
    calc(var(--stroke-width) * -0.3827) calc(var(--stroke-width) * -0.9239) 0
      var(--stroke-color),
    calc(var(--stroke-width) * 0) calc(var(--stroke-width) * -1) 0
      var(--stroke-color),
    calc(var(--stroke-width) * 0.3827) calc(var(--stroke-width) * -0.9239) 0
      var(--stroke-color),
    calc(var(--stroke-width) * 0.7071) calc(var(--stroke-width) * -0.7071) 0
      var(--stroke-color),
    calc(var(--stroke-width) * 0.9239) calc(var(--stroke-width) * -0.3827) 0
      var(--stroke-color);
}
// COLORS --------------------------------------
.clr-white{
  color: var(--clr-white);
}
.clr-pink{
  color: var(--clr-accent-pink);
}
// BACKGROUNDS --------------------------------
.bg-pink{
  background: var(--clr-bg-pink);
}
.bg-white{
  background: var(--clr-white);
}
.bg-gr-pink-dark{
  background: var(--clr-btn-bg-pink-dark);
}
.bg-gr-green-dark{
  background: var(--clr-btn-bg-green-dark);
}
.bg-gr-blue-dark{
  background: var(--clr-btn-bg-blue-dark);
}
// BORDER WIDTH -------------------------------
.bw-1{
  border-width: 1px;
}
.bw-3{
  border-width: 3px;
}
// BORDER STYLE -------------------------------
.br-solid{
  border-style: solid;
}
// BORDER COLOR -------------------------------
.bclr-white{
  border-color: var(--clr-white);
}
// BORDER RADIUS ------------------------------
.br-30{
  border-radius: 30px;
}
.br-40{
  border-radius: 40px;
}
.br-60-40{
  border-radius: clamp(40px, 2.0294rem + 2.3529vi, 60px);
}
// BOXSHADOWS --------------------------------
.box-s{
  box-shadow: 0 0 20px hsl(0 0% 0% / .035);
}
// LINKS -------------------------------------
@media(hover: hover){
  .hov-link:hover{
    text-decoration-color: currentColor;
  }
}
// MISC --------------------------------------
.fixed {
  overflow-y: hidden;
}

.visually-hidden {
  position: fixed;
  transform: scale(0);
}
// ANIMATION ---------------------------------
@keyframes circle1 {
  to {
    transform: rotate(1turn);
  }
}
@keyframes circle2 {
  to {
    transform: rotate(-1turn);
  }
}
.anim, .anim-circle-2, .anim-title, .anim-subtitle, .anim-list-item{
  opacity: .0;
  transform: translateY(5vw);
}
.circle1-container {
  opacity: .0;

}
// misc --------------------------------------
sup{
  display: inline-block;
  transform: translateY(-25%);
  font-size: 12px;
  font-size: 70%;
}
.blockquote{
  overflow: hidden;
  position: relative;
  padding: clamp(25px, 1.3507rem + 1.0588vi, 34px) clamp(37px, 2.1007rem + 1.0588vi, 46px);
  color: var(--clr-accent-pink);
  font-size: clamp(14px, 0.6868rem + 0.9412vi, 22px);
  font-weight: var(--fw-m);
  border-radius: clamp(40px, 2.0294rem + 2.3529vi, 60px);
  border: clamp(2px, 0.1015rem + 0.1176vi, 3px) solid transparent;
  background-image:
  linear-gradient(white, white),
  linear-gradient(-5deg, #F79397 28%, #FCD3D5 100%);
  background-origin: border-box;
  background-clip: padding-box, border-box;
  &__inner{
    position: relative;
    z-index: 1;
    display: flex;
    align-items: center;
    gap: 0 var(--gutter);
    &-img{
      flex-shrink: 0;
      aspect-ratio: .3255;
      inline-size: 32px;
    }
  }
  &::before{
    content: '';
    position: absolute;
    z-index: 0;
    inset: 0;
    background: var(--clr-btn-bg-pink-light);
  }
}
.title-br{
  br{
    display: none;
    @media (max-width: 768px) {
      display: inline;
    }
  }
}
.sources{
  columns: 2;
  gap: calc(var(--gutter) * 2);
  &>*+*{
    margin-block-start: 16px;
  }
  a{
    color: inherit;

    text-decoration: underline;
    text-underline-offset: 3px;
    transition: text-decoration-color var(--tr);
    @media(hover: hover){
      &:hover{
        font-weight: var(--fw-b);
      }
    }
    &:active{
      text-decoration-color: transparent;
    }
  }
  @media (max-width: 900px) {
    columns: 1;
  }
}
.card-bor-pink{
  background-image: linear-gradient(white, white),
    var(--clr-btn-bg-pink-dark);
  background-origin: border-box;
  background-clip: padding-box, border-box;
}
.card-bor-green{
  background-image: linear-gradient(white, white),
    var(--clr-btn-bg-green-dark);
  background-origin: border-box;
  background-clip: padding-box, border-box;
}
.card-bor-blue{
  background-image: linear-gradient(white, white),
    var(--clr-btn-bg-blue-dark);
  background-origin: border-box;
  background-clip: padding-box, border-box;
}
