.zabota{
  position: relative;
  display: grid;
  grid-template-columns: repeat(12,1fr);
  gap: var(--gutter);
  margin-block-start: clamp(15px, -0.1213rem + 5.2941vi, 60px);
  background-image: url('../img/misc/flower-2.webp');
  background-repeat: no-repeat;
  background-position: top right;
  background-size: auto auto;
  &__title{
    position: relative;
    grid-column: 1 / span 7;
    grid-row: 1 / span 1;
    margin-block-start: 86px;
  }
  &__text{
    position: relative;
    grid-column: 1 / span 6;
    grid-row: 2 / span 1;
    max-inline-size: 515px;
  }
  &__quote{
    justify-self: end;
    align-self: end;
    grid-column: 7 / -1;
    grid-row: 2 / span 1;
    max-inline-size: 624px;
  }
}
@media (max-width: 992px) {
  .zabota{
    margin-inline-end: calc(-1 * var(--minimum-content-padding));
    padding-inline-end: var(--minimum-content-padding);
    background-size: clamp(275px, 13.3051rem + 19.4118vi, 440px) auto;
    background-position: top right -100px;
    &__title{
      grid-column: 1 / -1;
    }
    &__text{
      grid-column: 1 / -1;
      grid-row: 3 / span 1;
      max-inline-size: unset;
    }
    &__quote{
      grid-column: 1 / -1;
      grid-row: 2 / span 1;
      max-inline-size: unset;
    }
  }
}
