.socials-wrap{
  display: flex;
  align-items: center;
  gap: 20px;
  margin-block-end: clamp(50px, 2.6544rem + 2.3529vi, 70px);
}
.socials{
  display: flex;
  gap: 10px;
  a{
    transition: opacity var(--tr);
    @media(hover: hover){
      &:hover{
        opacity: .7;
      }
    }
  }
  img{
    inline-size: 30px;
    block-size: 30px;
  }
}
