.info-sm{
  display: grid;
  grid-template-columns: repeat(12,1fr);
  gap: var(--gutter);
  &>*{
    align-self: center;
  }
  &__text{
    grid-column: 1 / span 5;
    grid-row: 1 / span 1;
  }
  &__blockquote{
    grid-column: 6 / -1;
    grid-row: 1 / span 1;
  }
}
@media (max-width: 992px) {
  .info-sm{
    grid-template-columns: 1fr;
    gap: 30px 0;
    &__text{
      grid-column: 1 / -1;
    }
    &__blockquote{
      grid-column: 1 / -1;
      grid-row: 2 / span 1;
    }
  }
}
