.obsled{
  display: grid;
  grid-template-columns: repeat(12,1fr);
  gap: var(--gutter);
  &__text{
    align-self: center;
    grid-column: 1 / span 6;
    grid-row: 1 / span 1;
    max-inline-size: 485px;
    &>*+*{
      margin-block-start: 20px;
    }
  }
  &__info{
    justify-self: end;
    grid-column: 7 / -1;
    grid-row: 1 / span 1;
    display: grid;
    max-inline-size: 524px;
    span:nth-child(3){
      justify-self: end;
    }
  }
}
@media (max-width: 1024px) {
  .obsled{
    grid-template-columns: 1fr;
    &__text{
      grid-column: 1 / -1;
      max-inline-size: unset;
    }
    &__info{
      justify-self: center;
      grid-column: 1 / -1;
      grid-row: 2 / span 1;
      max-inline-size: unset;
    }
  }
}
