.vazhno{
  display: grid;
  grid-template-columns: repeat(12,1fr);
  gap: clamp(31px, 1.2551rem + 3.4118vi, 60px) var(--gutter);
  &__title{
    grid-column: 1 / span 6;
    grid-row: 1 / span 1;
  }
  &__info{
    align-self: center;
    grid-column: 7 / -1;
    grid-row: 1 / span 1;
    display: grid;
    gap: clamp(24px, 1.3588rem + 0.7059vi, 30px);
    &-num{
      justify-self: center;
    }
  }
  &__blockquote{
    grid-column: 1 / -1;
    grid-row: 2 / span 1;
  }
}
@media (max-width: 992px) {
  .vazhno{
    grid-template-columns: 1fr;
    &__title{
      grid-column: 1 / -1;
    }
    &__info{
      grid-column: 1 / -1;
     grid-row: 2 / span 1;
     &-text{
      justify-self: center;
      inline-size: 60%;
     }
    }
    &__blockquote{
      grid-row: 3 / span 1;
    }
  }
}
@media (max-width: 429px) {
  .vazhno{

    &__info{
     &-text{
      inline-size: unset;
     }
    }

  }
}
