.cards-topic{
  display: grid;
  gap: clamp(60px, 2.8088rem + 4.7059vi, 100px) 0;
}
.card-topic{
  overflow: hidden;
  position: relative;
  display: grid;
  grid-template-rows: min-content;
  block-size: clamp(241px, 10.4037rem + 23.2941vi, 439px);
  padding: clamp(25px, 0.739rem + 4.1176vi, 60px);
  background: var(--clr-btn-bg-pink-dark);
  border: 3px solid var(--clr-white);
  &__title{
    position: relative;
    z-index: 1;
    max-inline-size: 725px;
    font-size: clamp(15px, 0.3493rem + 2.9412vi, 40px);
  }
  .btn{
    position: relative;
    z-index: 1;
    align-self: end;
  }
  &__img{
    position: absolute;
    z-index: 0;
    inset-inline-end: clamp(-220px, 7.4993rem + -29.0588vi, 27px);
    inset-block-start: 90px;
    aspect-ratio: 1.2477;
    inline-size: clamp(214px, 2.4103rem + 54.8235vi, 680px);
  }
  &:nth-child(2){
    h3{
      max-inline-size: 437px;
    }
  }
}

@media (max-width: 429px) {
  .card-topic{
    border-width: 2px;
    .btn{
      justify-self: center;
    }
    &__img{
      inset-inline-start: 50%;
      inset-inline-end: unset;
      transform: translateX(-50%);
    }
  }
}
