:root {
  --header-h: clamp(60px, 3.5147rem + 1.1765vi, 70px);
  --max-container: 1170px;
  --minimum-content-padding: 20px;
  --gutter: clamp(16px, 0.6706rem + 1.6471vi, 30px);
  --ff-prim: "Mulish", sans-serif;
  --ff-sec: "Unbounded", sans-serif;
  --fz-base: 16px;
  --fz-xs: 12px;
  --fz-s: 14px;
  --fw-r: 400;
  --fw-m: 500;
  --fw-b: 700;
  --fw-bl: 900;
  --lh-base: 1.3;
  // цвета
  --clr-accent-pink: #F79397;
  --clr-accent-pink-light: #FFF5F6;
  --clr-accent-pink-roza: #FCD3D5;
  --clr-disabled: #D9D9D9;

  --clr-accent-green: #92E0D0;
  --clr-accent-blue: #4DB1E8;

  --clr-disabled-text: #EDEDED;
  --clr-border: #d9d9d9;
  // фоны
  --clr-bg-pink: linear-gradient(90deg, #FFFDF8 0%, #FDEBEA 41%);
  // кнопки pink
  --clr-btn-bg-pink-light: linear-gradient(180deg, #FFFDF8 0%, #FDEBEA 69%);
  --clr-btn-bg-pink-light-active: linear-gradient(180deg, #F6BCBF 0%, #FDEBEA 88%);
  --clr-btn-bg-pink-dark: linear-gradient(-5deg, #F79397 28%, #FCD3D5 100%);
  --clr-btn-bg-pink-dark-active: linear-gradient(-5deg, #D7585E 28%, #F79397 100%);
  // кнопки green
  --clr-btn-bg-green-dark: linear-gradient(-25deg, #92E0D0 0%, #DBFFF8 100%);
  // кнопки blue
  --clr-btn-bg-blue-dark: linear-gradient(-25deg, #85D3FF 0%, #C9EBFF 100%);
  // border radius
  --br-30: clamp(20px, 1.0147rem + 1.1765vi, 30px);
  //
  --clr-dark: #000;
  --clr-white: #fff;
  --tr: .3s ease-in-out;
}
