.list{
  &__item{
    display: flex;
    gap: 0 10px;
    max-inline-size: 450px;
    &::before{
      content: '';
      flex-shrink: 0;
      aspect-ratio: 1;
      inline-size: clamp(10px, 0.3897rem + 1.1765vi, 20px);
      block-size: clamp(10px, 0.3897rem + 1.1765vi, 20px);
      background-color: var(--clr-white);
      border-radius: 100vw;
      transform: translateY(5px);
    }
  }
  &.auto-grid{
    --min-w: 400px;
  }
}
