html, body{
  block-size: 100%;
}
body {
  font-family: var(--ff-prim);
  font-size: var(--fz-base);
  line-height: var(--lh-base);
  color: var(--clr-dark);
}

section, .section {
  margin-block-end: clamp(40px, 1.0882rem + 7.0588vi, 100px);
}

a {
  text-decoration-color: transparent;
  text-underline-offset: 3px;
}

.container > *, .full > * {
  grid-column: content;
}
.container, .full {
--full-max-width: 1fr;
--content-max-width: 1170px;
--full: minmax( var(--minimum-content-padding), 1fr );
--content: min( var(--content-max-width), 100% - var(--minimum-content-padding) * 2 );
display: grid;
grid-template-columns: [full-start] var(--full) [content-start] var(--content) [content-end] var(--full)
  [full-end];
grid-template-rows: min-content;
}
.full { grid-column: full; }
.content { grid-column: content; align-self: start;}
