.footer{
  margin-block-start: auto;
  padding-block: clamp(24px, 1.3353rem + 0.8235vi, 31px) clamp(20px, 0.6382rem + 3.0588vi, 46px);
  background-color: #F79397;
  color: var(--clr-white);
  &__inner{
    display: grid;
    grid-template-columns: repeat(12,1fr);
    gap: 0 var(--gutter);
    &-logo{
      grid-column: 1 / span 6;
      grid-row: 1 span 1;
      inline-size: 194px;
      block-size: 55px;
    }
    &-title-block{
      grid-column: 1 / span 6;
      grid-row: 2 / span 2;
      max-inline-size: 544px;
      p{
        font-size: clamp(18px, 1.0779rem + 0.2353vi, 20px);
        margin-block-end: 5px;
      }
      a{
        color: inherit;
        font-size: clamp(12px, 0.5618rem + 0.9412vi, 20px);
        transition: text-decoration-color var(--tr);
      }
    }
    &-nav{
      grid-column: 7 / -1;
      grid-row: 2 / span 1;
      margin-block-end: clamp(24px, 3.3926rem + -2.5882vi, 46px);
    }
    &-extra{
      grid-column: 7 / -1;
      grid-row: 3 / span 1;
      display: flex;
      flex-wrap: wrap;
      gap: clamp(5px, 0.1949rem + 0.5882vi, 10px) var(--gutter);
      a{
        color: inherit;
        font-size: 12px;
        white-space: nowrap;
        transition: text-decoration-color var(--tr);
      }
    }
  }
}
.footer-menu{
  display: flex;
  flex-wrap: wrap;
  gap: calc(var(--gutter) / 2) var(--gutter);
  &__link{
    font-size: clamp(14px, 0.7809rem + 0.4706vi, 18px);
    color: inherit;
    transition: text-decoration-color var(--tr);
  }
}
@media (max-width: 768px) {
  .footer{
    &__inner>*{
      grid-column: 1 / -1;
      grid-row: unset;
    }
    &__inner{
      &-title-block{
        margin-block-end: 15px;
      }
    }
  }
}
