@font-face {
  font-family: "Mulish";
  src: url("../fonts/MulishRegular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Mulish";
  src: url("../fonts/MulishMedium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Mulish";
  src: url("../fonts/MulishBold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Unbounded";
  src: url("../fonts/UnboundedRegular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Unbounded";
  src: url("../fonts/UnboundedBold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Unbounded";
  src: url("../fonts/UnboundedBlack.woff2") format("woff2");
  font-weight: 900;
  font-display: swap;
  font-style: normal;
}

h1, .h1, h2, .h2, h3, .h3, h4, .h4{
  font-family: var(--ff-sec);
  font-weight: var(--fw-b);
  line-height: 1.1;
}

h1, .h1 {
  font-size: clamp(30px, 0.6985rem + 5.8824vi, 80px);
}

h2, .h2 {
  font-size: clamp(25px, 0.739rem + 4.1176vi, 60px);
}

h3, .h3 {
  font-size: clamp(20px, 0.7794rem + 2.3529vi, 40px);
}
h4, .h4 {
  font-size: clamp(15px, 0.8199rem + 0.5882vi, 20px);
  line-height: 1.2;
}
h5, .h5 {
  font-size: 20px;
  line-height: 1.2;
}
.text{
  font-size: clamp(12px, 0.6559rem + 0.4706vi, 16px);
}
