.buds{
  display: grid;
  grid-template-columns: repeat(12,1fr);
  gap: var(--gutter);
  &__title{
    grid-column: 1 / span 7;
    grid-row: 1 / span 1;
    max-inline-size: 652px;
  }
  &__desc{
    grid-column: 1 / span 7;
    grid-row: 2 / span 1;
    max-inline-size: 598px;
    &>*+*{
      margin-block-start: 20px;
    }
    span{
      font-weight: var(--fw-b);
      text-transform: uppercase;
    }

  }
  &__img{
    grid-column: 8 / -1;
    grid-row: 1 / span 2;
    block-size: 100%;
    img{
      block-size: 100%;
    }
  }
  &__info{
    grid-column: 1 / -1;
    grid-row: 3 / span 1;
    font-size: 44px;
    text-transform: uppercase;
  }
}
@media (max-width: 1210px) {
  .buds{
    &__info{
      font-size: 3.6vw;
    }
  }
}
@media (max-width: 900px) {
  .buds{
    grid-template-columns: 1fr;
    &__title{
      grid-column: 1 / -1;
      max-inline-size: unset;
    }
    &__img{
      justify-self: center;
      grid-column: 1 / -1;
      grid-row: 2 / span 1;
      max-inline-size: 417px;
    }
    &__desc{
      grid-column: 1 / -1;
      grid-row: 3 / span 1;
      max-inline-size: unset;
    }
    &__info{
      grid-row: 4 / span 1;
    }
  }
}
@media (max-width: 640px) {
  .buds{
    &__info{
      font-size: clamp(20px, 1.0147rem + 1.1765vi, 30px);
    }
  }
}
