.filter{
  --filter-h: 60px;
  position: relative;
  display: grid;
  inline-size: 100%;
  block-size: var(--filter-h);
  margin-block-end: 22px;
  &__option-wrap{
    justify-self: end;
    display: flex;
    justify-content: space-between;
    align-items: center;
    inline-size: 260px;
    block-size: var(--filter-h);
    padding-inline: 30px;
    border: 1px solid;
    border-radius: 30px;
    &[data-f="0"]{
      background: #FFF9FA;
      border-color: var(--clr-accent-pink);
    }
    &[data-f="1"]{
      background: linear-gradient(90deg, #FFF 0%, #FDEBEA 100%);
      border-color: var(--clr-accent-pink);
    }
    &[data-f="2"]{
      background: linear-gradient(90deg, #fff 0%, #D2FCF4 100%);
      border-color: var(--clr-accent-green);
    }
    &[data-f="3"]{
      background: linear-gradient(90deg, #fff 0%, #CEEEFF 100%);
      border-color: var(--clr-accent-blue);
    }
  }
  &__option{
    &.hide{
      display: none;
    }
  }
  &__menu{
    visibility: hidden;
    opacity: 0;
    transform: translateY(-10px);
    position: absolute;
    inset-inline-end: 0;
    inset-block-start: calc(var(--filter-h) + 10px);
    display: grid;
    justify-items: start;
    gap: 10px;
    inline-size: 260px;
    padding: 30px;
    background-color: var(--clr-white);
    border-radius: 30px;
    box-shadow: 0 0 20px hsl(0 0% 0% / .07);
    transition: opacity var(--tr), visibility var(--tr), transform var(--tr);
    button{
      transition: color var(--tr);
      @media(hover: hover){
        &:hover{
          color: var(--clr-accent-pink);
        }
      }
    }
    &.active{
      visibility: visible;
      opacity: 1;
      transform: translateY(0px);
    }
  }
  svg{
    inline-size: 24px;
    block-size: 24px;
    transition: transform var(--tr);
    &[data-f="0"], &[data-f="1"]{
      fill: var(--clr-accent-pink);
    }
    &[data-f="2"]{
      fill: var(--clr-accent-green);
    }
    &[data-f="3"]{
      fill: var(--clr-accent-blue);
    }
    &.rotate{
      transform: rotate(-180deg);
    }
  }
}
