.popup{
  --mis: clamp(20px, 0.45rem + 4vi, 54px);
  max-width: unset;
  max-height: unset;
  padding-inline: 10px;
  padding-block: var(--minimum-content-padding);
  background-color: transparent;
  block-size: 100%;
  border: none;
  &__inner{
    overflow: hidden;
    position: relative;
    max-inline-size: var(--max-container);
    block-size: calc(100vh - var(--minimum-content-padding) * 2);
  }
  &__content{
    padding-inline: var(--mis);
    overflow-y: auto;

    &::-webkit-scrollbar {
      width: 6px;
    }
    &::-webkit-scrollbar-track {
      background-color: transparent;
      border-radius: 100vw;
    }
    &::-webkit-scrollbar-thumb {
      background-color: var(--clr-accent-pink);
      border-radius: 100vw;
    }
  }
  &__section{
    margin-inline: calc(-1 * var(--mis));
    padding-inline: var(--mis);
  }
  &__close{
    position: absolute;
    inset-inline-end: clamp(20px, 0.4265rem + 4.1176vi, 55px);
    inset-block-start: clamp(20px, 0.7794rem + 2.3529vi, 40px);
    inline-size: 32px;
    block-size: 32px;
  }
  &__hero{
    position: relative;
    &-img{
      position: absolute;
      z-index: 0;
      inset-inline-start: 50%;
      transform: translateX(-50%);
      inset-block-end: 0px;
      inline-size: calc(100% + var(--mis) * 2);
      block-size: 100%;
      img{
        block-size: 100%;
      }
    }
  }
  .popup-container > *, .popup-full > * {
    grid-column: content;
  }
  .popup-container, .popup-full {
    --content: min( var(--max-container), 100% );
    display: grid;
    grid-template-columns:
    [full-start] 1fr [content-start] var(--content) [content-end] 1fr [full-end];
  }
  .popup-full { grid-column: full; }
  .popup-content { grid-column: content; }
}

dialog::backdrop {
  background-color: hsl(0 0% 0% / .75);
}

.hero-popup-1{
  padding-block-start: clamp(50px, 2.4191rem + 3.5294vi, 80px);
  padding-block-end: clamp(45px, 2.4191rem + 3.5294vi, 85px);
  .img{

  }
  .title{
    position: relative;
    z-index: 1;
    max-inline-size: 730px;
    margin-block-end: 10px;
  }
  .text{
    position: relative;
    z-index: 0;
    max-inline-size: 525px;
  }
}

.stats-block{
  align-self: start;
  display: grid;
    gap: 15px;
  .top{
    display: grid;
  }

}

.stats-popup-1{
  display: flex;
  gap: 20px;
  .left{
    display: grid;
    .numbers{
      display: flex;
      align-items: center;
      gap: clamp(10px, 0.5309rem + 0.4706vi, 14px);
    }
    .text{}
  }
  .right{
    display: grid;
    .numbers{
      display: flex;
      align-items: center;
      gap: clamp(10px, 0.5309rem + 0.4706vi, 14px);
    }
    .text{}
  }
}
.stats-popup-2{

}
@media (max-width: 880px) {
  .stats-popup-1{
    display: grid;
  }
}
.columns-2-1x1{
  display: grid;
  grid-template-columns: repeat(2,1fr);
  gap: var(--gutter);
  .left{
    grid-column: 1 / span 1;
    grid-row: 1 / span 1;
    .list{
      display: flex;
      flex-direction: column;
      gap: clamp(10px, 0.5074rem + 0.5882vi, 15px);
    }
  }
  .right{
    grid-column: 2 / -1;
    grid-row: 1 / span 1;
  }
}
@media (max-width: 992px) {
  .columns-2-1x1{
    grid-template-columns: 1fr;
    .left{
      grid-column: 1 / -1;
    }
    .right{
      grid-column: 1 / -1;
      grid-row: 2 / span 1;
    }
  }
}
.columns-2-2x1{
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: var(--gutter);
  .item-1{
    grid-column: 1 / span 1;
    grid-row: 1 / span 1;
  }
  .item-2{
    grid-column: 1 / span 1;
    grid-row: 2 / span 1;
  }
  .item-3{
    grid-column: 2 / -1;
    grid-row: 1 / span 2;
  }
}
@media (max-width: 992px) {
  .columns-2-2x1{
    .item-1{
      grid-column: 1 / -1;
    }
    .item-2{
      grid-column: 1 / -1;
      grid-row: 2 / span 1;
    }
    .item-3{
      grid-column: 1 / -1;
      grid-row: 3 / span 1;
    }
  }
}

.popup__btm{
  display: grid;
  grid-template-columns: repeat(12,1fr);
  gap: var(--gutter);
  &-text{
    grid-column: 1 / span 6;
    grid-row: 1 / span 1;
    &>*+*{
      margin-block-start: 20px;
    }
  }
  &-img{
    grid-column: 7 / -1;
    grid-row: 1 / span 1;
    img{
      block-size: 100%;
    }
  }
  &-btns{
    grid-column: 1 / -1;
    grid-row: 2 / span 1;
    display: flex;
    justify-content: space-between;
    gap: 20px;
  }
}
@media (max-width: 992px) {
  .popup__btm{
    grid-template-columns: 1fr;
    &-text{
      grid-column: 1 / -1;
    }
    &-img{
      justify-self: center;
      grid-column: 1 / -1;
      grid-row: 2 / span 1;
      max-inline-size: 471px;
      margin-block-end: 40px;
    }
    &-btns{
      grid-row: 3 / span 1;
      flex-direction: column;
      align-items: center;
      .btn{
        margin-inline-start: unset;
      }
    }
  }
}
.warning{
  font-size: 46px;
  @media (max-width: 1200px) {
    font-size: 3.8vw;
  }
  @media (max-width: 640px) {
    font-size: 24px;
  }
}

