.contacts{
  display: grid;
  grid-template-columns: repeat(12,1fr);
  gap: 25px var(--gutter);
  &__desc{
    align-self: start;
    grid-column: 1 / span 6;
    display: grid;
    gap: 15px;
    .address{
      display: flex;
      gap: 0 var(--gutter);
      font-style: normal;
      &__img{
        flex-shrink: 0;
        aspect-ratio: .8513;
        inline-size: clamp(22px, 1.1397rem + 1.1765vi, 32px);
      }
    }
    &-tel{
      display: flex;
      gap: 0 var(--gutter);
      padding-block: 5px;
      &::before{
        content: '';
        aspect-ratio: 1.3;
        inline-size: clamp(26px, 1.4132rem + 1.0588vi, 35px);
      }
    }
    &-mail{
      display: flex;
      gap: 0 var(--gutter);
      &-img{
        flex-shrink: 0;
        aspect-ratio: 1.3;
        inline-size: clamp(26px, 1.4132rem + 1.0588vi, 35px);
      }
      p{
        display: grid;
        a{
          color: inherit;
          text-decoration: underline;
          text-underline-offset: 3px;
          text-decoration-thickness: 1px;
          transition: text-decoration-color .3s ease-in-out;
          @media(hover: hover){
            &:hover{
              text-decoration-color: transparent;
            }
          }
        }
      }
    }
  }
  &__form{
    grid-column: 7 / -1;
    display: grid;
    gap: 10px;
  }
}
@media (max-width: 768px) {
  .contacts{
    grid-template-columns: 1fr;
    &__desc{
      grid-column: 1 / -1;
    }
    &__form{
      grid-column: 1 / -1;
    }
  }
}
