.tkani{
  display: grid;
  grid-template-columns: repeat(12,1fr);
  gap: var(--gutter);
  &__img{
    grid-column: 1 / span 6;
    grid-row: 1 / span 2;
    max-inline-size: 569px;
    align-self: center;
  }
  &__title{
    grid-column: 7 / -1;
    grid-row: 1 / span 1;
    align-self: end;
  }
  &__text{
    grid-column: 7 / -1;
    grid-row: 2 / span 1;
    align-self: start;
  }
}
@media (max-width: 900px) {
  .tkani{
    grid-template-columns: 1fr;
    &__img{
      grid-column: 1 / -1;
      grid-row: 1 / span 1;
      justify-self: center;
    }
    &__title{
      grid-column: 1 / -1;
      grid-row: 2 / span 1;
    }
    &__text{
      grid-column: 1 / -1;
      grid-row: 3 / span 1;
    }
  }
}
