// btn gradient ------------------
.btn{
  display: inline-grid;
  place-items: center;
  inline-size: clamp(170px, 8.5074rem + 10.5882vi, 260px);
  block-size: clamp(46px, 2.3103rem + 2.8235vi, 70px);
  font-size: clamp(12px, 0.6088rem + 0.7059vi, 18px);
  font-weight: var(--fw-b);
  border-radius: 100vw;
  border: 1px solid var(--clr-white);
  text-decoration-color: transparent;
  text-decoration-line: underline;
  text-underline-offset: 3px;
  cursor: pointer;
  transition: text-decoration-color var(--tr);
  @media(hover: hover){
    &:hover{
      text-decoration-color: currentColor;
    }
  }
  &:active{
    text-decoration-color: transparent;
  }
}
.btn--light{
  color: var(--clr-accent-pink);
  background: var(--clr-btn-bg-pink-light);
  &:active{
    background: var(--clr-btn-bg-pink-light-active);
  }
}
.btn--dark{
  color: var(--clr-white);
  background: var(--clr-btn-bg-pink-dark);
  &:active{
    background: var(--clr-btn-bg-pink-dark-active);
  }
}
.btn:disabled, .btn--disabled{
  color: var(--clr-disabled-text);
  background-color: var(--clr-disabled);
  cursor: not-allowed;
}

// btn plain ------------------
.btn-plain{
  inline-size: clamp(170px, 8.5074rem + 10.5882vi, 260px);
  block-size: clamp(46px, 2.5456rem + 1.6471vi, 60px);
  color: var(--clr-white);
  background-color: hsl(0 0% 100% / .3);
  font-size: clamp(12px, 0.6088rem + 0.7059vi, 18px);
  border: 1px solid var(--clr-white);
  border-radius: 100vw;
  text-decoration-color: transparent;
  text-decoration-line: underline;
  text-underline-offset: 3px;
  transition: text-decoration-color var(--tr);
  @media(hover: hover){
    &:hover{
      text-decoration-color: currentColor;
    }
  }
  &:active{
    color: var(--clr-accent-pink);
    background-color: hsl(0 0% 100% / .8);
  }
}

// btn gost ------------------
.btn-ghost{
  display: inline-grid;
  place-items: center;
  block-size: clamp(44px, 2.1382rem + 3.0588vi, 70px);
  padding-inline: clamp(25px, 1.0919rem + 2.3529vi, 45px);
  font-size: clamp(12px, 0.5618rem + 0.9412vi, 20px);
  line-height: 1.2;
  border-radius: 100vw;
}
.btn-ghost--white{
  color: var(--clr-white);
  border: clamp(2px, 0.1015rem + 0.1176vi, 3px) solid var(--clr-white);
}
.btn-ghost--pink{
  color: var(--clr-accent-pink);
  border: 1px solid var(--clr-accent-pink);
  text-decoration-color: transparent;
  text-decoration-line: underline;
  text-underline-offset: 3px;
  transition: background-color var(--tr), text-decoration-color var(--tr);
  @media(hover: hover){
    &:hover{
      background-color: var(--clr-accent-pink-light);
      text-decoration-color: currentColor;
    }
  }
}
