._404{
  display: grid;
  margin-inline-start: clamp(0px, -2.3529rem + 11.7647vi, 100px);
  margin-block-end: 20px;
  &__content{
    position: relative;
    grid-column: 1 / -1;
    grid-row: 1 / span 1;
    margin-block-start: clamp(254px, 14.8162rem + 5.2941vi, 299px);
    h1{
      font-size: clamp(100px, 3.6618rem + 12.9412vi, 210px);
      line-height: 1;
      --stroke-color: var(--clr-white);
      --stroke-width:5px;
    }
    p{
      --stroke-color: var(--clr-white);
      --stroke-width: 3px;
    }
  }
  &__img{
    grid-column: 1 / -1;
    grid-row: 1 / span 1;
    justify-self: end;
    aspect-ratio: 1.0442;
    inline-size: clamp(343px, 9.9316rem + 57.5294vi, 832px);
  }
}
@media (max-width: 500px) {
._404{
  overflow: hidden;
  margin-inline: calc(-1 * var(--minimum-content-padding));
  padding-inline: var(--minimum-content-padding);
  &__content{
    display: grid;
    justify-content: center;
    p{
      text-align: center;
    }
    .btn{
      justify-self: center;
    }
  }
  &__img{
    margin-inline-end: clamp(-100px, -1.0417rem + -16.6667vi, -70px);
  }
}
}
