.news-top{
  display: grid;
  grid-template-columns: repeat(12,1fr);
  gap: var(--gutter);
  margin-block-end: clamp(40px, 5.0809rem + -3.5294vi, 70px);
  &__img{
    grid-column: 1 / span 7;
    grid-row: 1 / span 1;
    aspect-ratio: 1.6291;
    img{
      block-size: 100%;
    }
  }
  &__aside{
    grid-column: 8 / -1;
    grid-row: 1 / span 1;
    .share-wrap{
      display: flex;
      justify-content: end;
      gap: clamp(15px, 0.3493rem + 2.9412vi, 40px);
      margin-block-end: 15px;
      .share{
        display: flex;
        gap: 10px;
        img{
          inline-size: 24px;
          block-size: 24px;
        }
      }
    }
    .extra{
      background-color: var(--clr-accent-pink-roza);
      border-radius: 40px;
    }
  }
}
@media (max-width: 768px) {
  .news-top{
    display: block;
    &__aside{
      margin-block-end: 20px;
    }
  }
}
.news-content{
  margin-block-end: clamp(15px, 0.5846rem + 1.7647vi, 30px);
  padding-block-end: clamp(60px, 2.8088rem + 4.7059vi, 100px);
  border-block-end: 1px solid var(--clr-accent-pink);
  h4{
    margin-block-end: 10px;
  }
  p{
    margin-block-end: 20px;
  }
  ul{
    list-style: disc;
    margin-block: 20px;
    padding-inline-start: 15px;
  }
}
